"use client";

import { ChevronDown, Gift, LayoutGrid } from "lucide-react";
import { ExploreDropdown } from "./ExploreDropdown";
import { useState } from "react";

export function NavBar() {
    const [isExploreOpen, setIsExploreOpen] = useState(false);

    return (
        <div className="bg-transparent py-4">
            <div className="container mx-auto px-4">
                <div className="relative max-w-6xl mx-auto">
                    <div className="bg-secondary text-white rounded-full px-2 py-2 flex items-center justify-between shadow-lg">

                        {/* Left Navigation */}
                        <div className="flex items-center">
                            {/* Explore trigger */}
                            <button
                                className="flex items-center gap-2 py-2 px-6 hover:text-primary transition-colors font-medium whitespace-nowrap"
                                onMouseEnter={() => setIsExploreOpen(true)}
                                onMouseLeave={() => setIsExploreOpen(false)}
                            >
                                <LayoutGrid className="w-4 h-4" />
                                <span>Explore</span>
                            </button>

                            <div className="h-6 w-px bg-white/10 mx-2"></div>

                            {/* Best Sales */}
                            <div className="flex items-center gap-1 py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap relative group">
                                <span>Best Sales</span>
                                <span className="bg-primary text-white text-[10px] px-1.5 py-0.5 rounded-full ml-1 font-bold">HOT</span>
                            </div>

                            {/* Special Offers */}
                            <div className="flex items-center gap-1 py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap">
                                <span>Community</span>
                                <ChevronDown className="w-3 h-3" />
                            </div>

                            {/* Quick find */}
                            <div className="flex items-center gap-1 py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap">
                                <span>Quick find</span>
                                <ChevronDown className="w-3 h-3" />
                            </div>

                            {/* News */}
                            <div className="py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap">
                                <span>Support</span>
                            </div>

                            {/* Contact */}
                            <div className="py-2 px-4 hover:text-primary transition-colors cursor-pointer whitespace-nowrap">
                                <span>Team</span>
                            </div>
                        </div>

                        {/* Right CTA */}
                        <div className="hidden md:block pr-1">
                            <button className="bg-primary text-white font-bold py-2.5 px-6 rounded-full flex items-center gap-2 hover:bg-orange-600 transition-all duration-300 shadow-lg transform hover:-translate-y-0.5">
                                <Gift className="w-5 h-5" />
                                <span>Campus Deals</span>
                            </button>
                        </div>
                    </div>

                    {/* Dropdown positioned relative to navbar container */}
                    <div
                        onMouseEnter={() => setIsExploreOpen(true)}
                        onMouseLeave={() => setIsExploreOpen(false)}
                    >
                        <ExploreDropdown isOpen={isExploreOpen} />
                    </div>
                </div>
            </div>
        </div>
    );
}
